@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

#aboutMe {
  background-color: #e1e1e1;
  padding: 0 40px 30px 40px;
  scroll-margin-top: 10vh;
}

.aboutMe__title {
  padding-top: 10px;
  padding-bottom: 0;
  color: #1a1a1d;
}

.aboutMe__title > h1 {
  font-size: 40px;
  text-decoration: underline;
}

.aboutMe__title > h3 {
  font-size: 25px;
  margin-bottom: 10px;
  padding: 0;
  color: #444cda;
}

.aboutMe__text {
  padding-bottom: 20px;
  font-size: 18px;
  text-align: justify;
  opacity: .8;
}

.aboutMe__text > p {
  padding-top: 0;
  margin-top: 0;
}

.aboutMe__specs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-top: 10px;
  padding-bottom: 20px;
}

.aboutMe__specs > div {
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.aboutMe__specs > div:nth-child(3),
.aboutMe__specs > div:nth-child(4) {
  padding-top: 10px;
}

.aboutMe__specs > div > label {
  font-weight: 600;
  padding-right: 5px;
  font-size: 18px;
}

.aboutMe__specs > div > span {
  font-size: 18px;
  opacity: .7;
}

.aboutMe__skills {
  margin-top: 30px;
  padding-bottom: 20px;
}

.aboutMe__skills > h1 {
  font-size: 40px;
  text-decoration: underline;
  margin-bottom: 15px;
}

.aboutMe__skillsDetails {
  margin-top: 10px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-items: flex-start;
}

.aboutMe__skillsDetails > div > h3 {
  font-size: 20px;
  color: #444cda;
  margin-bottom: 30px;
}

.aboutMe__skillsDetails > div > p {
  font-size: 18px;
  opacity: .7;
  border-bottom: 2px solid #444cda;
  padding-top: 0px;
  padding-bottom: 10px;
}

.aboutMe__eduWork {
  padding-bottom: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 50px;
  gap: 50px;
}

.aboutMe__eduWork > div > h1 {
  font-size: 40px;
  margin-top: 10px;
  text-decoration: underline;
}

.aboutMe__eduWork > div > div {
  margin-top: 30px;
  background-color: #efefef;
  border-radius: 10px;
  box-shadow: 0px 0px 5px .1px #444cda;
}

@media only screen and (max-width: 768px) {
  #aboutMe {
    padding: 0 10px 30px 10px;
  }

  .aboutMe__skillsDetails {
    grid-template-columns: 1fr 1fr;
  }

  .aboutMe__skillsDetails > div {
    padding-right: 15px;
  }

  .aboutMe__skillsDetails > div > p {
    padding-bottom: 5px;
  }

  .aboutMe__eduWork {
    grid-template-columns: 1fr;
    grid-gap: 20px;
    gap: 20px;
  }
}
.aboutMe__timeline {
  margin-left: 10px !important;
  padding-top: 20px !important;
}

.aboutMe__timeline .MuiTimelineItem-missingOppositeContent::before {
  display: none;
}

.aboutMe__timelineContent {
  margin-top: -3px !important;
}

.aboutMe__timelineContent > div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
}

.aboutMe__timelineContent > div > span {
  padding-left: 5px;
  opacity: .7;
}

.aboutMe__timelineContent > p {
  font-size: 18px;
  opacity: .8;
  text-align: justify;
}

.aboutMe__timelineContent:last-child {
  padding-bottom: 30px;
}

.aboutMe__timelineDot,
.aboutMe__timelineConnector {
  background-color: #444cda !important;
}
#contactMe {
  background-color: #e1e1e1;
  margin-top: 0;
  padding: 40px 40px;
  scroll-margin-top: 10vh;
}

#contactMe > h1 {
  padding: 0;
  margin: 0;
  text-align: center;
  text-decoration: underline;
  font-size: 40px;
}

.contactMe__form {
  padding: 20px 0 20px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.contactMe__form > fieldset {
  padding-top: 10px;
  padding-bottom: 0;
  border: 2px solid #444cda;
  margin-bottom: 10px;
  border-radius: 5px;
}

.contactMe__form > fieldset > legend {
  padding-left: 10px;
  padding-right: 10px;
  font-size: 18px;
  opacity: .8;
}

.contactMe__form > fieldset > input, 
.contactMe__form > fieldset > textarea {
  /* font-family: 'Itim'; */
  font-size: 15px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 400px;
  padding: 10px;
  border: 2px solid #444cda40;
  border-radius: 5px;
  outline: none;
  resize: none;
  font-family: 'Roboto', sans-serif;
}

.contactMe__form > fieldset > textarea {
  height: 100px;
}

.contactMe__submit {
  width: 200px;
  height: 40px;
  cursor: pointer;
  letter-spacing: 2px;
  color: #1a1a1d;
  border: 2px solid #1a1a1d;
  background-color: #444cda;
  font-size: 16px;
  font-weight: 700;
  border-radius: 5px;
  padding: 5px 0;
  text-align: center;
  margin-top: 20px;
  transition: background-color .3s ease-in-out;
}

.contactMe__submit:hover {
  border: 2px solid #444cda;
  background-color: #e1e1e1;
  transition: background-color .3s ease-in-out;
}

.contactMe__submit:active {
  top: 1px;
  position: relative;
}

.contactMe__social {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.contactMe__social > h3 {
  font-size: 25px;
}

.contactMe__social > div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

.contactMe__icon {
  color: #1a1a1d;
  width: 50px !important;
  height: 80px !important;
  margin: 0 20px 0 20px;
  transition: -webkit-transform .5s ease-in-out;
  transition: transform .5s ease-in-out;
  transition: transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
}

.contactMe__icon:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}


@media only screen and (max-width: 768px) {
  #contactMe {
    padding: 40px 10px;
  }

  #contactMe > h1 {
    text-align: left;
  }

  .contactMe__form > fieldset {
    width: 90%;
  }

  .contactMe__form > fieldset > input, 
  .contactMe__form > fieldset > textarea {
    width: 90%;
  }

  .contactMe__icon {
    color: 1a1a1d;
    width: 90% !important;
    height: 50px !important;
    margin: 0 !important;
    padding: 0 !important;
    transition: none;
  }
  
  .contactMe__icon:hover {
    -webkit-transform: none;
            transform: none;
  }
}
.footer__container {
  font-size: 24px;
  text-align: center;
  height: 80px;
  background-color: #2f2f2f;
  color: #e1e1e1;
  padding-right: 40px;
  padding-left: 40px;
  padding-top: .1px;
}

@media only screen and (max-width: 768px) {
  .footer__container {
    font-size: 16px;
    padding-top: 13px;
  }
}
.home {
  width: 100%;
  height: 90vh;
  margin-top: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  background: #2f2f2f;
}

.home__settings {
  -webkit-align-self:flex-end;
          align-self:flex-end;
  margin-top: 0;
}

.home__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: #e1e1e1;
}

.home__container > h1 {
  font-size: 45px;
  margin: 0;
  padding-top: 20px;
  letter-spacing: 8px;
}

.home__container > h2 {
  font-size: 35px;
  padding: 10px;
  margin: 0;
  letter-spacing: 5px;
}

.home__container > img {
  width: 150px;
  border: 10px solid #444cda;
  border-radius: 50%;
  box-shadow: 0px 0px 10px 0px #444cda;
  transition: width 1s ease-in-out;
}

.home__container > img:hover {
  width: 250px;
}

.MuiSvgIcon-fontSizeSmall {
  margin-top: 30px;
  font-size: 2.2rem !important;
}

.MuiIconButton-label {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column !important;
          flex-direction: column !important;
}

.home__resumeLabel {
  margin: 0 !important;
  padding: 0 !important;
  font-size: 16px;
  opacity: .9;
  font-style: italic;
}

.home__resume {
  padding: 0;
  width: 90%;
  height: auto;
  object-fit: cover !important;
  overflow: scroll !important;
}

@media only screen and (max-width: 768px) {
  .home__container {
    margin-top: -13vh;
  }

  .home__container > h1 {
    letter-spacing: 0;
    font-size: 35px;
  }

  .home__container > h2 {
    letter-spacing: 0;
    font-size: 20px;
  }

  .home__resume {
    padding: 0;
    width: 100%;
  }

  .MuiSvgIcon-root {
    height: 0.5em !important;
    display: block !important;
  }
}
.navigationBar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.navigationBar__nav {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  top: 0;
  width: 100%;
  height: 10vh;
  z-index: 1;
  background-color: #444cda;
  /* border-bottom: 2px solid var(--light); */
}

.navigationBar__nav > ul {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  list-style: none;
}

.navigationBar__nav > ul > li > a {
  text-decoration: none;
  color: #000000;
  font-weight: 700;
  letter-spacing: 3px;
  padding-right: 40px;
  transition: color .2s ease-in-out;
}

.navigationBar__nav > ul > li > a:hover {
  color: #e1e1e1;
  transition: color .2s ease-in-out;
}

.navigationBar__hamburguer {
  display: none !important;
}

@media only screen and (max-width: 768px) {
  .navigationBar__nav > ul {
    display: none;
  }

  .navigationBar__hamburguer {
    display: inline-block !important;
  }

  .MuiSvgIcon-root {
    font-size: 3.5rem !important;
    width: 1.5em !important;
  }

  .navigationBar__link {
    text-decoration: none;
    color: #000000;
    font-weight: 700;
    letter-spacing: 3px;
    padding-right: 40px;
    transition: color .2s ease-in-out;
  }
  
  .navigationBar__link:hover {
    color: #e1e1e1;
    transition: color .2s ease-in-out;
  }

  .MuiList-root {
    background-color: #444cda;
  }
}
.projectCard__card {
  background-color: #eceff1 !important;
  border-radius: 5px !important;
  border: 1px solid #e1e1e1 !important;
  box-shadow: 0px 0px 5px 3px #444cda !important;
  height: -webkit-max-content;
  height: max-content;
}

.projectCard__cardActionArea {
  overflow: hidden;
  width: 100%;
  cursor: default !important;
}

.projectCard__img {
  display: block;
  width: 100%;
  max-height: 230px;
  object-fit: contain;
  object-position: top;
  transition: max-height 3s !important;
}

.projectCard__img:hover {
  max-height: 1000px;
  transition: max-height 3s !important;
}

.projectCard__cardContent {
  padding-top: 0 !important;
}

.projectCard__cardContent > p {
  text-align: justify;
  font-size: 15px;
  opacity: .8;
}

.projectCard__cardContent > div > span {
  opacity: .8;
  font-size: 12px;
}

.projectCard__cardActions {
  padding-top: 0 !important;
  padding-left: 15px !important;
}

.projectCard__anchor {
  color: #1a1a1d !important;
  text-decoration: none !important;
}

.projectCard__cardButton:hover {
  background-color: #444cda !important;
}
#projects {
  background-color: #2f2f2f;
  margin-top: 0;
  padding: 0 40px 60px 40px;
  scroll-margin-top: 10vh;
}

.projects__container > h1 {
  padding-top: 40px;
  margin-top: 0;
  padding-bottom: 20px;
  text-decoration: underline;
  font-size: 40px;
  color: #e1e1e1;
}

.projects__cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  -webkit-flex-direction: row;
          flex-direction: row;
  grid-gap: 25px;
  gap: 25px;
}

@media only screen and (max-width: 768px) {
  #projects {
    padding: 0 10px 60px 10px;
  }

  .projects__cards {
    grid-template-columns: 1fr;
  }
}
